<template>
    <div id="view-dog">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{name}}</h4>
            </li>            
            <li class="collection-item">Dog ID# {{dog_id}}</li>
            <li class="collection-item">Age: {{age}}</li>
            <li class="collection-item">Sex: {{sex}}</li>
        </ul>
        <router-link class="btn grey" to="/">Back</router-link>
        <button @click="deleteDog" class="btn red">Delete Dog</button>

        <div class="fixed-action-btn">
            <router-link :to="{name: 'edit-dog', params: {dog_id: dog_id}}" 
                        class="btn-floating bt-large red" title="Create A New Dog">
                        <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'view-dog',
    data () {
        return {
            dog_id: null,
            name: null,
            age: null,
            sex: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('dogs').where('dog_id', '==', to.params.dog_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.dog_id = doc.data().dog_id
                    vm.name = doc.data().name
                    vm.age = doc.data().age
                    vm.sex = doc.data().sex
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('dogs').where('dog_id', '==', this.$route.params.dog_id)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.dog_id = doc.data().dog_id
                    this.name = doc.data().name
                    this.aged = doc.data().age
                    this.sex = doc.data().sex
                })
            })
        },
        deleteDog () {
            if (confirm('Are You Shure?')) {
            db.collection('dogs').where('dog_id', '==', this.$route.params.dog_id)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
            })
            }
        }
    }
}
</script>