<template>
    <div id="edit-dog">
        <div class="row">
            <form @submit.prevent="updateDog" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="number" v-model.number="dog_id" placeholder="Enter Dog ID#..." required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" placeholder="Enter Dog Name..." required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model.number="age" placeholder="Enter Dog Age..." required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="sex" placeholder="Enter Dog Sex..." required>
                    </div>
                </div>
                <button type="submit" class="btn">Edit Dog</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template> 
 
<script>
import db from './firebaseInit'
export default {
    name: 'edit-dog',
    data () {
        return {
            dog_id: null,
            name: null,
            age: null,
            sex: null
        }
    },
       beforeRouteEnter (to, from, next) {
        db.collection('dogs').where('dog_id', '==', to.params.dog_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.dog_id = doc.data().dog_id
                    vm.name = doc.data().name
                    vm.age = doc.data().age
                    vm.sex = doc.data().sex
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('dogs').where('dog_id', '==', this.$route.params.dog_id)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.dog_id = doc.data().dog_id
                    this.name = doc.data().name
                    this.aged = doc.data().age
                    this.sex = doc.data().sex
                })
            })
        },
        updateDog () {
            db.collection('dogs').where('dog_id', '==', this.$route.params.dog_id)
            .get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                   doc.ref.update({
                       dog_id: this.dog_id,
                       name: this.name,
                       age: this.age,
                       sex: this.sex
                   })
                   .then (() => {
                       this.$router.push({name: 'view-dog', params: {dog_id: this.dog_id}})
                   })
                })
            })
        }
    }
}
</script>