<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Dogs</h4></li>
            <li v-for="dog in dogs" 
            :key="dog.id" 
            class="collection-item">
            <div class="chip">{{dog.dog_id}}</div> {{dog.name}}  |  age: {{dog.age}}  | sex: {{dog.sex}}
            <router-link 
            class="secondary-content"
            :to="{name: 'view-dog', params: {dog_id: dog.dog_id}}">
            <i class="fa fa-eye"></i>
            </router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" 
                        class="btn-floating bt-large red" title="Create A New Dog">
                        <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data () {
        return {
            dogs: []
        }
    },
    created () {
        db.collection('dogs').orderBy('dog_id').get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'dog_id': doc.data().dog_id,
                    'name': doc.data().name,
                    'age': doc.data().age,
                    'sex': doc.data().sex
                }
                this.dogs.push(data)
            })
        })
    }
}
</script>