<template>
    <div id="new-dog">
        <h3>New Dog</h3>
        <div class="row">
            <form @submit.prevent="saveDog" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model.number="dog_id" placeholder="Enter Dog ID#..." required>
                    </div>
                    <label>Dog ID#</label>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" placeholder="Enter Dog Name..." required>
                    </div>
                    <label>Name</label>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="number" v-model.number="age" placeholder="Enter Dog Age..." required>
                    </div>
                    <label>Age</label>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="sex" placeholder="Enter Dog Sex..." required>
                    </div>
                    <label>Sex</label>
                </div>
                <button type="submit" class="btn">Submit Dog</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-dog',
    data () {
        return {
            dog_id: null,
            name: null,
            age: null,
            sex: null
        }
    },
    methods: {
        saveDog () {
            db.collection('dogs').add({
                dog_id: this.dog_id,
                name: this.name,
                age: this.age,
                sex: this.sex
            })
            .then (docRef => this.$router.push('/'))
            .catch (error => console.log(error))
        }
    }
}
</script>