import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import EditDog from '@/components/EditDog'
import ViewDog from '@/components/ViewDog'
import NewDog from '@/components/NewDog'
import Login from '@/components/Login'
import Register from '@/components/Register'
import firebase from 'firebase'

 
Vue.use(Router)

let router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/edit',
      name: 'edit-dog',
      component: EditDog,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/new',
      name: 'new-dog',
      component: NewDog,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/:dog_id',
      name: 'view-dog',
      component: ViewDog,
      meta: {
        requiresAuth: true
      }

    }
  ]
})

// Nav GuARDS

router.beforeEach((to, from, next) => {
  // check for required Auth guard
  if(to.matched.some(record => record.meta.requiresAuth)) {
    // check if not logged in
    if(!firebase.auth().currentUser) {
      // go to login page
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
      }  else {
        // proceed to route
        next();
      }
    } else if (to.matched.some(record => record.meta.requiresAuth)) {
       // check if logged in
    if(firebase.auth().currentUser) {
      // go to home page
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      });
      }  else {
        // proceed to route
        next();
      }
    } else {
        // proceed to route
        next();
    }
  }
);

export default router;